<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 系统用户管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                        type="primary"
                        icon="el-icon-delete"
                        class="handle-del mr10"
                        @click="handleAdd"
                >添加</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
<!--                <el-table-column prop="Seq" label="序号" width="55" align="center"></el-table-column>-->
                <el-table-column prop="UserName" label="用户名" width="100"></el-table-column>
                <el-table-column label="操作" width="140" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                class="red"
                                @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="90%">
            <el-form ref="form" :model="form" :rules="rules" label-width="100px">
                <el-form-item label="用户名" prop="UserName">
                    <el-input v-model="form.UserName"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    // import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    // import 'quill/dist/quill.bubble.css';
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';

    export default {
        name: 'GoodsList',
        data() {
            return {
                query: {
                    userId: 0,
                    userName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {Id:0,UserName:''},
                idx: -1,
                id: -1,

                rules: {
                    UserName: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                        { min: 1, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur' }
                    ]
                },

            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;

        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        components: {
            VueCropper,
        },
        methods: {
            //鼠标单击的事件
            onClick(e, editor) {
                console.log('Element clicked')
                console.log(e)
                console.log(editor)
            },
            //清空内容
            clear() {
                this.$refs.editor.clear()
            },
            // 准备富文本编辑器
            onEditorReady (editor) {},
// 富文本编辑器 失去焦点事件
            onEditorBlur (editor) {},
// 富文本编辑器 获得焦点事件
            onEditorFocus (editor) {},
// 富文本编辑器 内容改变事件
            onEditorChange (editor) {},
            // 富文本编辑器 点击插入图片或者视频
            insertImgClick (e) {
                if (e.target.className.indexOf('icon-pic') != -1) {
                    document.getElementById('insert_image').click();
                } else if (e.target.className.indexOf('icon-video') != -1) {
                    document.getElementById('insert_video').click();
                }
            },
            handleUploadSuccess(response, file, fileList){
                let quill = this.$refs.myQuillEditor.quill
                let length = quill.getSelection().index;
                quill.insertEmbed(length, 'image', response)
                quill.setSelection(length + 1)
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'sys/getsysuserlist',  method:'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 删除操作
            handleDelete(index, row) {
                // 二次确认删除
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        var data = {
                            id:row.Id
                        }
                        request({
                            url:'sys/delsysuser',  method:'post',
                            data:data
                        }).then((res)=>{
                            console.log(res);
                            if (res.code==200){
                                this.$message.success('删除成功');
                                this.tableData.splice(index, 1);
                                this.getData();
                            } else {
                                this.$message.error('删除失败：'+res.msg);
                            }
                        });

                    })
                    .catch(() => {});
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                console.log(row);
                this.form = row;
                this.editVisible = true;
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={

                }
                this.form = aFilm;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                if (this.idx===-1) {
                    var data = {
                        jsonData:JSON.stringify(this.form)
                    }
                    request({
                        url:'sys/addsysuser', method: 'post',
                        data:data
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`添加成功`);
                            this.getData();
                        } else {
                            this.$message.error('添加失败：'+res.msg);
                        }
                    });
                } else { 
                    request({
                        url:'sys/updatesysuser', method: 'post',
                        data:this.form
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                            this.$set(this.tableData, this.idx, this.form);
                        } else {
                            this.$message.error('更新失败：'+res.msg);
                        }
                    });

                }
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }
</style>
